.event-list {
  @include pie-clearfix;
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    margin-bottom: 8px; }

  .event-item {
    @include pie-clearfix;
    display: block;
    position: relative;
    color: $main-text-color;
    text-decoration: none;
    background-color: $light-background-color;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .15);

    &:hover {
      .event-action {
        opacity: .9; } } }

  .event-preview-photo {
    position: relative;
    overflow: hidden;
    padding-top: 56.25%;
    background: $main-text-color resolve('logo-gray.svg') no-repeat center;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0), rgba(0, 0, 0, .7)); }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%; } }

  .event-content {
    position: relative; }

  .event-info {
    font-size: 13px;
    padding: 8px 68px 8px 8px; }

  .event-title {
    position: absolute;
    top: -48px;
    right: 0;
    left: 0;
    padding: 0 8px;
    width: 100%;
    color: white;

    .title {
      overflow: hidden;
      margin: 0;
      text-overflow: ellipsis;
      white-space: nowrap; } }

  .event-meta {

    .label {
      padding-right: 8px;
      font-weight: 500;
      color: $sub-text-color; }

    .data {
      font-weight: 500; } }

  .event-action {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 64px;
    background-color: $main-color;
    transition: opacity .25s;

    &.detail {
      background-color: $main-text-color; } }

  .action-status {
    position: absolute;
    top: calc(50% - 12px);
    left: 0;
    right: 0;
    text-align: center;
    font-weight: 500;
    color: white; }

  @include tablet {
    li {
      float: left;
      width: calc(50% - 12px);
      margin: 8px 8px 0;

      &:nth-child(2n) {
        margin-left: 0; }

      &:last-child {
        margin-bottom: 8px; } } }

  @include desktop {
    li {
      float: none;
      width: 100%;
      margin: 0;
      padding: 16px 16px 0;

      &:last-child {
        padding-bottom: 16px; } }

    .event-item {
      box-shadow: none; }

    .event-preview-photo {
      float: left;
      width: 375px;
      min-height: 211px;
      padding-top: 0;

      img {
        position: relative;
        display: block; } }

    .event-content {
      display: block;
      overflow: hidden;

      &:before {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: $divider-color; }

      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: $divider-color; } }

    .event-info {
      padding: 16px 148px 16px 16px;
      height: 209px; }

    .event-title {
      position: relative;
      top: 0;
      padding: 0;
      color: $main-text-color;

      .title {
        font-size: 24px; }

      .location {
        font-size: 15px; } }

    .event-meta {
      margin-top: 60px;
      font-size: 15px; }

    .event-action {
      width: 148px;
      font-size: 24px;

      .action-status {
        top: auto;
        bottom: 20px;
        line-height: 38px;

        &:after {
          content: '';
          display: inline-block;
          vertical-align: top;
          margin-left: 8px;
          width: 24px;
          height: 38px;
          background: resolve('event-item-button.svg') no-repeat; } } } } }
