.payment {
  max-width: 800px;
  margin: 0 auto;

  .payment-process {
    position: relative;
    min-height: 74px;
    margin-bottom: 24px;

    &:before {
      content: '';
      position: absolute;
      top: 16px;
      left: 6%;
      width: 44%;
      height: 4px;
      background-color: $sub-text-color; }

    &:after {
      content: '';
      position: absolute;
      top: 16px;
      left: 50%;
      width: 44%;
      height: 4px;
      background-color: $sub-text-color; }

    .payment-step {
      position: absolute;
      z-index: 10;
      min-width: 70px;
      font-weight: bold;
      text-align: center;
      color: $sub-text-color;

      &:before {
        display: block;
        margin: 0 auto 8px;
        width: 36px;
        height: 36px;
        line-height: 36px;
        color: white;
        font-weight: bold;
        background-color: $sub-text-color;
        border-radius: 5px; } }

    .step1 {
      left: 0%;

      &:before {
        content: '1'; } }

    .step2 {
      left: calc(50% - 35px);

      &:before {
        content: '2'; } }

    .step3 {
      right: 0%;

      &:before {
        content: '3'; } }

    &.is-step1-active {
      .step1 {
        color: $main-text-color;

        &:before {
          background-color: $main-color; } } }

    &.is-step2-active {
      &:before {
        background-color: $main-color; }

      .step1, .step2 {
        color: $main-text-color;

        &:before {
          background-color: $main-color; } } }

    &.is-step3-active {
      &:before, &:after {
        background-color: $main-color; }

      .step1, .step2, .step3 {
        color: $main-text-color;

        &:before {
          background-color: $main-color; } } } }

  .payment-item-info {
    @include pie-clearfix;
    margin: 8px 0;
    padding: 0;
    list-style: none;

    .name, .quantity {
      display: inline-block;
      padding-right: 8px; }

    .price {
      float: right;

      &.is-deleted {
        text-decoration: line-through; } } }

  .use-bonus {
    margin: 16px 0;

    input[type='checkbox'] {
      margin-right: 8px; } }

  .use-bonus-detail {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-left: 8px;
    width: 22px;
    height: 22px;
    background-color: $light-main-color;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color .25s;

    &:hover {
      background-color: lighten($light-main-color, 5%); }

    &:before {
      position: absolute;
      top: 1px;
      left: 0;
      right: 0;
      text-align: center;
      content: '?';
      color: $main-color;
      font-weight: 500; } }

  .get-ticket-method {
    @include pie-clearfix;
    margin: 8px 0;

    input[type='radio'] {
      margin-right: 8px; }

    .method {
      display: inline-block; }

    .price {
      float: right; } }

  .paymant-total-info {
    @include pie-clearfix;
    margin: 8px 0;

    .label {
      display: inline-block; }

    .price {
      float: right; } }

  .payment-atm-info {
    margin: 24px 0;
    padding: 16px;
    font-weight: 500;
    color: $important-color;
    border: 2px solid $important-color;
    border-radius: 5px;

    span {
      display: block; } }

  .payment-details {
    margin: 24px 0;
    padding: 0;
    list-style: none;

    .detail-item {
      margin-bottom: 8px; }

    span {
      display: block; }

    .label {
      margin-bottom: 4px;
      font-size: 13px;
      font-weight: bold;
      color: $main-color; }

    @include tablet {
      span {
        display: inline-block; }

      .label {
        min-width: 80px;
        margin-bottom: 0;
        font-size: 16px; } } } }
