.account {
  max-width: 480px;
  margin: 0 auto;

  .sns-signin {
    padding-top: 16px;
    border-top: 1px solid $divider-color;

    .signin-facebook {
      position: relative;
      color: white;
      background-color: #3c5999;
      border-color: #3c5999;

      &:before {
        content: '';
        position: absolute;
        top: 6px;
        left: 6px;
        width: 24px;
        height: 24px;
        background: resolve('icon_24_fb_signin.svg') no-repeat; } }

    .signin-line {
      position: relative;
      color: white;
      background-color: #00b900;
      border-color: #00b900;

      &:before {
        content: '';
        position: absolute;
        top: 6px;
        left: 6px;
        width: 24px;
        height: 24px;
        background: resolve('icon_24_line_signin.svg') no-repeat; } } }

  .message {
    text-align: center;
    margin-bottom: 24px;

    .message-image {
      display: block;
      margin: 0 auto 16px; }

    .message-content {
      margin-bottom: 12px;

      .important {
        display: block;
        font-size: 21px;
        line-height: 1.8;
        font-weight: 500; } } }

  .sub-content {
    margin-bottom: 16px;
    font-size: 13px;
    text-align: center;

    .link-button {
      display: inline-block;
      font-weight: 500;
      color: $main-color; } }

  .photo-preview {
    position: relative;
    width: 100%;
    margin-bottom: 24px;
    padding-top: 63.3%;

    .photo-preview-inner {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border: 2px solid $sub-text-color;
      border-radius: 3px;

      &:after {
        content: '請上傳您的學生證';
        position: absolute;
        display: block;
        top: calc(50% - 12px);
        width: 100%;
        font-size: 18px;
        font-weight: 500;
        line-height: 24px;
        text-align: center;
        color: $sub-text-color; }

      img {
        position: relative;
        z-index: 10;
        width: 100%;
        height: auto; } } }

  @include tablet {
    .message {
      .message-content {
        margin: 0 64px 12px; } } } }
