.footer {
  padding-top: 48px;
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  color: $mid-text-color;

  .footer-item {
    @include pie-clearfix;
    padding: 8px 0; }

  .logo {
    width: 153px;
    height: 48px;
    margin: 0 auto 16px;
    text-indent: -9990px;
    background: resolve('logo-gray.svg') no-repeat; }

  .info {
    span {
      display: block; } }

  .link {
    margin-bottom: 4px;

    a {
      display: inline-block;
      width: 24px;
      height: 24px;
      margin: 0 4px; }

    .youtube {
      background: resolve('link-youtube.svg') no-repeat; }

    .soundcloud {
      background: resolve('link-soundcloud.svg') no-repeat; }

    .mail {
      background: resolve('link-mail.svg') no-repeat; } }

  @include tablet {
    text-align: left;

    .footer-item {
      padding: 16px 0; }

    .footer-item + .footer-item {
      border-top: 1px solid $sub-text-color; }

    .logo {
      float: left;
      margin: 0; }

    .info {
      float: right;
      margin: 6px 0;

      .info-item {
        display: inline-block; }

      .info-item + .info-item {
        margin-left: 12px;
        padding-left: 16px;
        border-left: 1px solid $sub-text-color; } }

    .link {
      float: right;
      margin-bottom: 0; }

    .copyright {
      float: left;
      margin-top: 4px; } } }

