$mq-responsive: true;
$mq-breakpoints: map-get($theme, breakpoints);

@if $env == 'dev' {
  $mq-show-breakpoints: (phone, tablet, desktop, wide) !global; }

// edit breakpoints in /config/shared.yaml
// to see more options -> https://github.com/sass-mq/sass-mq
$mq-landscape: '(orientation: landscape)';
$mq-hidpi: '(-webkit-min-device-pixel-ratio: 1.3), (min-resolution: 120dpi), (min-resolution: 1.3dppx)';

$xx: rgba(red, .1);

$main-color: #df643e;
$deep-main-color: #c85937;
$light-main-color: #f8e0d8;
$background-color: #f5f2ee;
$light-background-color: #fcfbf9;
$important-color: #d0011b;
$success-color: #acdc7b;
$main-text-color: #272929;
$mid-text-color: #676767;
$sub-text-color: #b0b3b2;
$divider-color: #dfe0e0;
$seat-available-color: #acdc7b;
$area-color: #f8e0d8;

$color-blue: #24bbea;
$color-blue-dark: #1fa7d0;
$color-black: $main-text-color;
$color-black-light: lighten($main-text-color, 10%);
$color-gray: $mid-text-color;
$color-gray-light: lighten($mid-text-color, 10%);
$color-red: $important-color;
