.seat-detail {
  max-width: 800px;
  margin: 0 auto;

  .seat-area-preview {
    position: relative;
    padding-top: 56.25%;

    &:before {
      content: '';
      position: absolute;
      z-index: 10;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0), rgba(0, 0, 0, .5)); }

    .seat-preview-photo {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%; }

    .seat-area-name {
      position: absolute;
      z-index: 11;
      left: 16px;
      right: 16px;
      bottom: 16px;
      color: white;
      font-weight: 500; } }

  .seat-position-preview {
    overflow: scroll;
    max-height: 50vh;
    margin: 24px 0; }

