.header {
  @include pie-clearfix;
  position: relative;
  text-align: center;

  .logo {
    display: block;
    position: relative;
    z-index: 1001;
    display: inline-block;
    width: 126px;
    height: 56px;
    margin: 0;
    text-indent: -9990px;
    background: resolve('logo-small.svg') no-repeat;
    background-size: contain;

    h1 {
      margin: 0;
      font-size: 21px; } }

  .menu {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    z-index: 1000;
    margin: 0;
    padding: 56px 0 0;
    font-size: 15px;
    background-color: white;
    opacity: 0;
    transition: opacity .5s, height .5s;

    li {
      display: block;
      border-top: 1px solid $divider-color;

      &:last-child {
        border-bottom: 1px solid $divider-color; }

      // &.member
      //// border-top: 1px solid $light-main-color

      .member-name {
        display: inline-block;
        padding: 16px 0;
        line-height: 24px;
        font-weight: 500;
        color: $main-color;

        &:before {
          content: '';
          display: inline-block;
          vertical-align: middle;
          width: 16px;
          height: 16px;
          margin: 3px 8px 5px 0;
          color: $sub-text-color;
          background: resolve('icon_16_account.svg') no-repeat; } } }

    &.collapse {
      display: none;

      &.in {
        display: block;
        opacity: 1; } }

    &.collapsing {
      display: block;
      opacity: 0; } }

  .menu-button-wrap {
    position: absolute;
    top: 0;
    right: 0; }

  .menu-button {
    box-sizing: content-box;
    position: relative;
    z-index: 1001;
    width: 24px;
    height: 24px;
    padding: 16px;

    span {
      display: block;
      position: absolute;
      width: 24px;
      height: 3px;
      top: 50%;
      background-color: $main-color;
      border-radius: 3px;
      transition: background-color .25s;

      &:before, &:after {
        content: '';
        display: block;
        position: absolute;
        width: 24px;
        height: 3px;
        background-color: $main-color;
        border-radius: 3px;
        transition: transform .25s; }

      &:before {
        top: -8px;
        transform-origin: top left; }

      &:after {
        top: 8px;
        transform-origin: bottom left; } } }

  .menu-item {
    display: inline-block;
    text-align: center;
    padding: 16px 12px;
    line-height: 24px;
    font-weight: 500;
    color: $main-text-color;
    text-decoration: none;
    transition: color .25s, background-color .25s;

    &:hover {
      color: $main-color; } }

  @include tablet {
    .logo {
      float: left; }

    .menu {
      float: right;
      position: relative;
      height: auto;
      padding: 0;
      vertical-align: middle;
      opacity: 1;
      background-color: transparent;
      transition: none;

      li {
        display: inline-block;
        border: 0;

        .member-name {
          padding: 8px 0 8px 24px;
          border-top: 0;
          border-left: 1px solid $sub-text-color; } }

      &.collapse {
        display: block; } }

    .menu-item {
      display: inline-block; }

    .menu-button-wrap {
      display: none; } }

  @include desktop {
    .logo {
      width: 140px;
      height: 76px;
      background: resolve('logo.svg') no-repeat; }

    .menu {
      font-size: 16px;

      li:last-child {
        border-bottom: 0; } }

    .menu-item {
      padding: 26px 16px; } } }

.menu-active {
  height: 100%;
  overflow: hidden;

  body {
    height: 100%;
    overflow: hidden; }

  .menu-button {
    span {
      background-color: transparent;

      &:before {
        transform: rotate(45deg); }

      &:after {
        transform: rotate(-45deg); } } } }
