.event-detail {
  max-width: 800px;
  margin: 0 auto;

  .session-list {
    list-style: none;
    margin: 0;
    padding: 0;

    .session-head {
      @include pie-clearfix;
      display: none;
      margin-bottom: 8px;
      padding-bottom: 4px;
      font-size: 13px;
      border-bottom: 1px solid $divider-color;

      span {
        font-weight: 500;
        color: $sub-text-color; } }

    .session {
      @include pie-clearfix;
      padding: 8px;
      font-size: 13px;
      line-height: 26px;
      transition: background-color .25s;

      &:hover {
        background-color: $background-color; }

      span {
        display: block;

        &:before {
          display: inline-block;
          min-width: 72px;
          color: $sub-text-color;
          font-weight: 500; } }

      .time:before {
        content: '場次時間'; }

      .location:before {
        content: '演出場地'; }

      .status:before {
        content: '售票狀態'; }

      .price:before {
        content: '票價'; }

      .action:before {
        content: '購票'; }

      .buy-button {
        display: inline-block;
        padding: 2px 8px;
        font-size: 13px;
        font-weight: 500;
        line-height: 1.4;
        text-decoration: none;
        color: white;
        background-color: $important-color;
        border-radius: 5px;
        transition: background-color .25s;

        &.is-disable {
          pointer-events: none;
          background-color: $sub-text-color;

          &:hover {
            background-color: $sub-text-color; } }

        &:hover {
          background-color: lighten($important-color, 5%); } } }

    .session + .session {
      border-top: 1px solid $divider-color; } }

  @include tablet {
    .session-list {
      .session-head {
        display: block; }

      .session {
        padding: 8px 4px;

        span:before {
          display: none; }

        .buy-button {
          padding: 4px 8px; } }

      .time {
        float: left;
        width: 27%;
        padding-left: 4px; }

      .location {
        float: left;
        width: 27%; }

      .status {
        float: left;
        width: 15%; }

      .price {
        float: left;
        width: 15%; }

      .action {
        float: left;
        width: 15%; }

      .session + .session {
        border-top: 0; } } }

  @include desktop {
    .session-list {
      .session {
        font-size: 15px; } } } }
