.install-app {
  display: table-cell;
  vertical-align: middle;
  width: 100vw;
  height: 100vh;
  background: resolve('install-app-bg.png') center no-repeat;
  background-size: cover;

  .install-app-content {
    width: 300px;
    margin: 0 auto; }

  .logo {
    margin: 0 auto;
    width: 219px;
    height: 68px;
    text-indent: -9999px;
    background: resolve('logo-white.svg') no-repeat; }

  .text {
    margin: 36px 0;
    text-align: center;
    color: white;
    font-weight: 500; } }
