.member {
  max-width: 640px;
  margin: 0 auto 24px;

  .member-menu {
    @include pie-clearfix;
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      margin: 0;
      padding: 8px;
      border-bottom: 1px solid $divider-color;
      transition: border .25s;

      &:hover {
        border-bottom-color: $main-color; } }

    .menu-item {
      display: block;
      text-decoration: none; }

    .menu-icon {
      display: inline-block;
      vertical-align: middle; }

    .menu-info {
      display: inline-block;
      vertical-align: middle;

      .title {
        margin: 0;
        color: $main-color; }

      .description {
        font-size: 15px;
        color: $sub-text-color; } } }

  .ticket-class {
    padding-left: 8px;
    font-size: 18px;
    font-weight: 500;
    color: $main-color;
    border-left: 8px solid $main-color; }

  .transaction-history {
    margin: 0;
    padding: 0;
    list-style: none; }

  .transaction-history-item {
    display: block;
    padding: 8px;
    color: $main-text-color;
    text-decoration: none;
    border-bottom: 1px solid $divider-color;
    transition: border .25s, background-color .25s;

    &:hover {
      border-bottom-color: $main-color;
      background-color: $background-color; }

    .item-row {
      @include pie-clearfix;

      .title {
        display: inline-block;
        width: 70%;
        overflow: hidden;
        font-weight: 500;
        white-space: nowrap;
        text-overflow: ellipsis; }

      .amount, .cash {
        float: right; }

      .label {
        padding-right: 8px;
        font-size: 13px;
        color: $sub-text-color; }

      .item-content, .location {
        font-size: 13px; } } }

  .transaction-history-details {
    margin: 24px 0;
    padding: 0;
    list-style: none;
    font-size: 13px;

    .detail-item {
      @include pie-clearfix;
      margin-bottom: 8px;

      .button {
        float: right;
        margin-top: -2px; } }

    span {
      display: inline-block; }

    .label {
      margin-right: 8px;
      font-weight: bold;
      color: $sub-text-color; }

    .detail-content {
      margin: 8px 0; }

    .detail-info {
      margin-bottom: 8px;

      .title {
        font-size: 16px;
        font-weight: 500; } }

    .detail-row {
      @include pie-clearfix;
      line-height: 1.5;

      .name {
        max-width: 70%; }

      .amount {
        margin-left: 8px;
        vertical-align: top; }

      .cash {
        float: right;

        &.is-delete {
          text-decoration: line-through; } } } }

  .bonus-detail {
    margin: 24px auto;
    text-align: center; }

  .bonus-icon {
    width: 96px;
    height: 96px;
    margin: 0 auto;
    background: resolve('icon_96_bonus.svg') no-repeat; }

  .bonus-amount {
    margin-top: 24px;

    .amount {
      font-size: 36px;
      font-weight: 300;
      color: $main-color; } }

  .bonus-history {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 13px;
    line-height: 1.5;

    .bonus-history-head {
      margin-bottom: 8px;
      padding: 4px;
      border-bottom: 1px solid $divider-color;

      span {
        font-weight: 500;
        color: $sub-text-color; } }

    .bonus-history-info {
      @include pie-clearfix;
      padding: 4px;
      transition: background-color .25s;

      &:hover {
        background-color: $background-color; } }

    span {
      display: inline-block; }

    .date-and-title {
      width: 60%;

      .title {
        display: block; } }

    .change {
      width: 20%;
      vertical-align: top;
      text-align: right; }

    .total {
      width: 20%;
      vertical-align: top;
      text-align: right; } }

  .question-item {
    border-bottom: 1px solid $divider-color; }

  .question-content {
    position: relative;
    display: block;
    text-decoration: none;
    color: $main-text-color;

    &:after {
      content: '';
      position: absolute;
      top: 8px;
      right: 8px;
      width: 24px;
      height: 24px;
      background: resolve('icon_36_up_arrow.svg') no-repeat; }

    &.collapsed {
      &:after {
        background: resolve('icon_36_down_arrow.svg') no-repeat; } }

    p {
      width: calc(100% - 24px);
      margin: 0;
      padding: 16px 8px;

      &:before {
        content: 'Q.';
        display: inline-block;
        padding-right: 8px;
        color: $main-color;
        font-weight: 500; } } }

  .answer-content {
    background-color: $background-color;

    p {
      width: calc(100% - 24px);
      margin: 0;
      padding: 16px 8px;

      &:before {
        content: 'A.';
        display: inline-block;
        padding-right: 8px;
        color: $main-color;
        font-weight: 500; } } }

  .service {
    &-phone, &-mail {
      &:before {
        content: '';
        display: inline-block;
        vertical-align: top;
        margin-right: 8px;
        width: 24px;
        height: 24px; } }

    &-phone {
      &:before {
        background: resolve('icon_24_service_phone.svg') no-repeat; } }

    &-mail {
      &:before {
        background: resolve('icon_24_service_mail.svg') no-repeat; } } }


  @include tablet {
    .member-menu {
      li {
        float: left;
        margin: 0 8px;
        width: calc(50% - 16px); } }

    .transaction-history-details {
      font-size: 16px;

      .detail-info {
        .title {
          font-size: 18px;
          font-weight: 500; } } }

    .bonus-history {
      font-size: 16px; } } }
