.ticket {
  position: relative;
  max-width: 640px;
  overflow: hidden;
  margin: 24px auto;
  color: $main-text-color;
  background-color: white;
  box-shadow: 0 0 3px rgba(0, 0, 0, .15);
  border-radius: 10px;

  .ticket-title {
    display: block;
    position: relative;
    padding-top: 56.25%;
    background-color: $main-text-color;

    &:before {
      content: '';
      position: absolute;
      z-index: 10;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0), rgba(0, 0, 0, .7)); }

    &:hover {
      opacity: .95; } }

  .ticket-image {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%; }

  .ticket-name {
    position: absolute;
    z-index: 11;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 8px;
    font-size: 16px;
    font-weight: bold;
    color: white; }

  .ticket-meta {
    position: relative;
    padding: 8px; }

  .ticket-meta-row {
    @include pie-clearfix;

    span {
      display: block;
      font-size: 13px;
      font-weight: 500;

      &:before {
        display: inline-block;
        padding-right: 8px;
        color: $sub-text-color; } }

    .address {}
    .location {
      font-size: 18px;
      font-weight: 500;
      padding-bottom: 8px;

      &:before {
        font-size: 13px; } }

    .seat {
      font-size: 18px;
      color: $main-color;

      &:before {
        content: '座位';
        font-size: 13px; } }

    .price {
      &:before {
        content: '票價'; } }

    .time {
      &:before {
        content: '演出時間';
        font-size: 13px; } }

    .number {
      &:before {
        content: '票號'; } } }

  .status {
    display: inline-block;
    overflow: hidden;
    width: 100%;
    font-weight: 500;
    font-size: 13px;
    color: $important-color;
    white-space: nowrap;
    text-overflow: ellipsis;

    &:before {
      content: '票券狀態';
      display: inline-block;
      padding-right: 8px;
      font-size: 13px;
      color: $sub-text-color; }

    .button {
      margin-right: 8px; } }

  &.detail {
    margin-top: 0;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .25);
    animation: ticket-printer 1s normal forwards;
    animation-iteration-count: 1;

    .ticket-meta-row {
      .time {
        font-size: 18px; } }

    .ticket-qr-code {
      display: block;
      margin: 0 auto;
      width: 100%;
      max-width: 250px; } }

  @include tablet {
    .ticket-name {
      padding: 16px;
      font-size: 20px; }

    .ticket-meta {
      padding: 16px; }

    .ticket-meta-row {
      .ticket-meta-left {
        float: left; }

      .ticket-meta-right {
        float: right;
        min-width: 150px; }

      span {
        font-size: 16px;

        &:before {
          font-size: 16px; } }

      .location {
        font-size: 18px;
        padding: 0; }

      .seat {
        font-size: 18px;

        &:before {
          font-size: 16px; } } }

    .status {
      font-size: 16px;

      &:before {
        font-size: 16px; } }

    &.detail {
      .ticket-meta-row {
        .time {
          font-size: 18px;

          &:before {
            font-size: 16px; } } } } } }


.ticket-printer {
  margin-left: -12px;
  margin-right: -12px;
  height: 48px;
  background-color: $main-text-color;
  border: 8px solid $divider-color;
  border-bottom-width: 18px;
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .25); }

.ticket-wrap {
  overflow: hidden;
  margin-top: -36px;
  margin-left: -16px;
  margin-right: -16px;
  padding: 0 16px; }

@keyframes ticket-printer {
  0% {
    transform: translateY(-100%); }
  100% {
    transform: translateY(0%); } }
