.input-item {
  @include pie-clearfix;
  margin-bottom: 16px;
  border-bottom: 1px solid $sub-text-color;

  &.info {
    border-bottom: 1px solid $divider-color; }

  &.textarea {
    border-bottom: 0;

    label {
      float: none;
      display: block; }

    textarea {
      width: 100%;
      height: 8rem;
      font-size: 16px;
      line-height: 1.2;
      border: 2px solid $sub-text-color;
      border-radius: 5px; } }

  .button {
    &.inline {
      float: right;
      margin: 10px 0;
      font-size: 13px; } }


  label {
    float: left;
    padding-right: 12px;
    line-height: 44px;
    font-weight: 500;
    color: $main-text-color;

    &.is-necessary {
      &:after {
        content: '*';
        color: $important-color; } }

    &.width-2 {
      width: calc(16px * 2 + 14px); }

    &.width-3 {
      width: calc(16px * 3 + 14px); }

    &.width-4 {
      width: calc(16px * 4 + 14px); }

    &.width-5 {
      width: calc(16px * 5 + 14px); }

    &.width-6 {
      width: calc(16px * 6 + 14px); }

    &.width-7 {
      width: calc(16px * 7 + 14px); }

    &.width-8 {
      width: calc(16px * 8 + 14px); } }

  .input {
    display: block;
    overflow: hidden;
    line-height: 44px;

    &.select {
      background: resolve('icon_44_down_arrow.svg') top right no-repeat; }

    input[type='text'], input[type='email'], input[type='password'], input[type='number'], input[type='file'] {
      width: 100%;
      padding: 10px 0;
      line-height: 24px;
      border: 0;
      outline: 0;
      background-color: transparent;

      &.center {
        text-align: center; } }

    &.half {
      display: inline-block;
      width: calc(50% - 4px); }

    select {
      width: 100%;
      height: 44px;
      line-height: 24px;
      border: 0;
      outline: 0;
      background-color: transparent;
      background-image: none;
      box-shadow: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;

      &:focus {
        outline: 0; } } } }

button, .button {
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  color: $main-color;
  background-color: white;
  border-radius: 3px;
  border: 2px solid $main-color;
  outline: 0;
  transition: color .25s, background .25s, border .25s;

  &:hover {
    color: lighten($main-color, 5%);
    border-color: lighten($main-color, 5%); }

  &.primary {
    color: white;
    background-color: $main-color;

    &:hover {
      background-color: lighten($main-color, 5%);
      border-color: lighten($main-color, 5%); } }

  &.is-disable {
    background-color: $divider-color;
    border-color: $divider-color;
    pointer-events: none;

    &:hover {
      background-color: $divider-color;
      border-color: $divider-color; } } }

.input-item + button {
  margin-top: 24px; }

a.button {
  display: block;
  line-height: 36px;

  &.inline {
    display: inline-block;
    margin: 0;
    padding: 4px 8px;
    width: auto;
    height: auto;
    line-height: 1;
    border-width: 1px; } }
