.section {
  .section-title {
    margin: 0 0 24px;
    padding-left: 8px;
    font-size: 20px;
    font-weight: 500;
    color: $main-color;
    border-left: 8px solid $main-color; }

  .section-content {
    margin-bottom: 48px;

    p, ol, ul {
      line-height: 1.7; } }

  @include tablet {
    .section-title {
      font-size: 24px; } } }

.youtube-video {
  position: relative;
  padding-top: 56.25%;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%; } }
