// rwd alias ----------------------------

@mixin tablet {
  @include mq(tablet) {
    @content; } }

@mixin desktop {
  @include mq(desktop) {
    @content; } }

@mixin landscape {
  @include mq($and: $mq-landscape) {
    @content; } }

@mixin retina {
  @include mq($and: $mq-hidpi) {
    @content; } }

// image ----------------------------

// asset-related functions from: https://github.com/assetsjs/postcss-assets

@mixin retina-image($url, $ext) {
  $image: $url + $ext;
  $size: size($image);
  width: width($image);
  height: height($image);
  background-image: resolve($image);
  @include retina {
    background-image: resolve($url + '@2x' + $ext);
    background-size: $size; } }

@mixin retina-background($url, $ext, $bg-size: cover) {
  $image: $url + $ext;
  background-image: resolve($image);
  background-size: $bg-size;
  @include retina {
    background-image: resolve($url + '@2x' + $ext);
    background-size: $bg-size; } }

// debug ----------------------------

@mixin xx($opacity: .2) {
  outline: 1px solid rgba(red, $opacity); }

// alias ----------------------------

@mixin input-placeholder-color($color) {
  &::-webkit-input-placeholder {
    color: $color;
    -webkit-font-smoothing: antialiased; }

  &:-moz-placeholder {
    color: $color; }

  &::-moz-placeholder {
    color: lighten($color, 10%); }

  &:-ms-input-placeholder {
    color: $color; } }

// layout ----------------------------

@mixin pie-clearfix {
  &:after {
    content: '';
    display: table;
    clear: both; } }

