.page-header {
  padding: 40px 16px;
  color: white;
  text-align: center;
  background: resolve('page-header-bg.jpg') no-repeat;
  background-size: cover;

  .page-header-inner {
    .page-title {
      margin: 0;
      font-size: 24px;
      font-weight: 500; } }

  .page-sub-title {
    display: block;
    font-size: 13px;
    font-weight: 500; }

  &.event-title {
    position: relative;
    padding-top: 30%;
    overflow: hidden;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, .7)); }

    .page-header-inner {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 16px;

      .page-title {
        font-size: 18px; } } }

  @include tablet {
    padding: 64px 24px;

    .page-header-inner {
      .page-title {
        font-size: 30px; } }

    .page-sub-title {
      font-size: 18px; }

    &.event-title {
      .page-header-inner {
        bottom: 32px;

        .page-title {
          font-size: 30px; } } } } }
