.area-detail {
  max-width: 800px;
  margin: 0 auto;

  .area-preview {
    overflow: scroll;
    max-height: 50vh;
    margin: 24px 0; }

  .area-list {
    list-style: none;
    margin: 0;
    padding: 0;

    .area-head {
      @include pie-clearfix;
      display: none;
      margin-bottom: 8px;
      padding-bottom: 4px;
      font-size: 13px;
      border-bottom: 1px solid $divider-color;

      span {
        font-weight: 500;
        color: $sub-text-color; } }

    .area-info {
      @include pie-clearfix;
      padding: 8px;
      font-size: 13px;
      line-height: 26px;
      transition: background-color .25s;

      &:hover {
        background-color: $background-color; }

      span {
        display: block;
        margin: 4px 0;

        &:before {
          display: inline-block;
          min-width: 128px;
          color: $sub-text-color;
          font-weight: 500; } }

      .area {
        .color {
          display: inline-block;
          vertical-align: text-top;
          width: 16px;
          height: 16px; }

        .area-name {
          display: inline-block; }

        &:before {
          content: '票區'; } }

      .amount:before {
        content: '剩餘數量'; }

      .status:before {
        content: '目前最多連號座位數'; }

      .price:before {
        content: '票價'; }

      .quantity {

        input[type=number] {
          width: 26px;
          height: 26px;
          vertical-align: middle;
          text-align: center;
          border: 0; }

        input[type=number]::-webkit-inner-spin-button,
        input[type=number]::-webkit-outer-spin-button {
          -webkit-appearance: none;
          margin: 0; }

        .quantity-button {
          display: inline-block;
          vertical-align: middle;
          cursor: pointer;
          width: 26px;
          height: 26px;
          text-indent: -9990px;
          opacity: .5;

          &:hover {
            opacity: 1; }

          &.minus {
            background: resolve('icon_26_minus.svg') no-repeat; }

          &.plus {
            background: resolve('icon_26_plus.svg') no-repeat; } }


        &:before {
          content: '張數'; } }

      .action:before {
        content: '購票'; }

      .buy-button {
        display: inline-block;
        padding: 2px 8px;
        font-size: 13px;
        font-weight: 500;
        line-height: 1.4;
        text-decoration: none;
        color: white;
        background-color: $important-color;
        border-radius: 5px;
        transition: background-color .25s;

        &.is-disable {
          pointer-events: none;
          background-color: $sub-text-color;

          &:hover {
            background-color: $sub-text-color; } }

        &:hover {
          background-color: lighten($important-color, 5%); } } }

    .area-info + .area-info {
      border-top: 1px solid $divider-color; } }

  @include tablet {
    .area-list {
      .area-head {
        display: block; }

      .area-info {
        padding: 8px 4px;

        span {
          margin: 0;

          &:before {
            display: none; } }

        .buy-button {
          padding: 4px 8px; } }

      .area {
        float: left;
        width: 12%;
        padding-left: 4px; }

      .amount {
        float: left;
        width: 15%; }

      .status {
        float: left;
        width: 25%; }

      .price {
        float: left;
        width: 12%; }

      .quantity {
        float: left;
        width: 20%; }

      .action {
        float: left;
        width: 15%; }

      .area-info + .area-info {
        border-top: 0; } } }

  @include desktop {
    .area-list {
      .area-info {
        font-size: 15px; } } } }
