.sign-tab, .tab {
  @include pie-clearfix;
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    position: relative;
    float: left;
    width: 50%;
    text-align: center;
    border-bottom: 1px solid $divider-color;
    transition: border .25s;

    &:after {
        content: '';
        position: absolute;
        left: calc(50% - 10px);
        bottom: 0;
        border: 10px solid transparent;
        transition: border .25s; }

    &.active {
      border-bottom-color: $main-color;

      &:after {
        border-bottom: 10px solid $main-color; }

      a {
        color: $main-color; } }

    a {
      display: block;
      padding: 16px;
      font-weight: 500;
      color: $main-text-color;
      text-decoration: none;
      transition: color .25s;

      &:hover {
        color: $main-color; } } } }

.tab-content {
  padding: 16px 0; }

.tab-pane {
  display: none;

  &.active {
    display: block; }

  &.fade {
    opacity: 0;
    transition: opacity .25s;

    &.in {
      opacity: 1; } } }
